*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;

}
.main_div{
  width:100%;
  height:100vh;
  background: #6983aa;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.center_div{
  width:25%;
  height:80vh;

  background-color: #f4f4f4;
  box-shadow: 5px 5px 25px -5px rgba(0,0,0,0.5);
  border-radius: 15px;

}
h1{
  color:white;
  background: transparent;
  background-color: #8566aa;
  padding: 6px 0 2px 0;
  margin-top: 20px;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.5);
}
input{
  text-align: center;
  height:30px;
  top: 10px;
  border: none;
  background: transparent;
  font-size: 20px;
  font-weight: 500;
  width:60%;
  border-bottom: 2px solid #8566aa;
  outline:none;
  

}
button{
  
  min-height:40px;
  width:40px;
  border-radius: 45%;
  border-color: transparent;
  background-color: #8566aa;
  color:#fff;
  font-size: 30px;
  border:none;
  margin-left: 10px;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);
}
button:hover{
  background-color: #20bf6b;
}
ol{
  margin-top:30px;
}
ol li{
  padding-left: 0px;

/* padding: 3px 0; */

text-align: left;

font-size: 40px;

font-weight: 500;

min-height: 20px;

display: flex;

align-items: center;

color: #8566aa;

text-transform: capitalize;

}
li{
  margin-left: 0%;

}
todo_style{
  padding-left: 0%;
  margin: 0%;
}
